import { ReactMediaRecorder } from "react-media-recorder";
import { Client } from "https://cdn.jsdelivr.net/npm/@gradio/client/dist/index.min.js";
import { useReactMediaRecorder } from "react-media-recorder";
import { useEffect, useRef, useState } from "react";
import VoiceRecorder from "../../Components/Recorder/Recorder";
import App from "../../Components/Recorder/Recorder";
import "./Reading.css";
const Reading = () => {
    const [img, setImg] = useState('');
    const [result, setResult] = useState('');
    const [desc_text, setDesc_text] = useState("");
    const [isFlipped, setIsFlipped] = useState(false);
    const fileInputRef = useRef(null);
    const [msg, setMsg] = useState("");
    const [topic, setTopic] = useState("Egypt");


    const handleAPIGETRequest = async () => {
        const client = await Client.connect("YoussefA7med/English_Helper_Reading");
        const result = await client.predict("/generate_passage_only", {
            topic: topic,
        }).then((result) => {
            // console.log(result.data)
            setMsg(result.data)
        })
        console.log(msg);

    }
    const predict = async (blobUrl, blob) => {
        console.log("Predicting with audio blob:", blob);
        if (!blob) {
            console.error("No audio blob available for prediction.");
            return;
        }
        const client = await Client.connect("YoussefA7med/English_Helper_Spoken");
        const result = await client.predict("/predict", {
            image_url: "",
            audio_file: blob,
            model: "meta-llama/llama-3.2-11b-vision-instruct:free",
        });
        console.log("Prediction Result:", result);
    };

    const { status, startRecording, stopRecording, mediaBlobUrl } =
        useReactMediaRecorder({
            audio: true,
            onStop: predict,
        });


    return (
        <div className="container justify-content-center my-3 ">
            <div className={`row custom-card ${isFlipped ? "flipped" : ""}`}>
                <div className="col-12  flip front ">
                    <h1 className="text-center"> Test your Reading skills</h1>
                    {!msg[0] && (
                        <div className="d-flex justify-content-center align-items-center">
                            <input id="topic" type="text" onChange={(e)=>setTopic(e.target.value)} name="topic" className=" mt-4 form-control w-50 input-lg border-1 p-3 rounded-pill Teal-bor-col" placeholder="Enter Topic!" aria-label="Username" aria-describedby="basic-addon1" />
                            <button  onClick={handleAPIGETRequest} className="mx-3 px-3 py-3 btn border-1 btn-signup rounded-pill align-self-end" type="button">Generate Passage</button>
                        </div>
                    )
                    }
                    {msg[0] && (
                        <>
                            <p className="border  p-4 border-1 custom-border-color text-black rounded-5">{msg[0]}</p>
                            <App message={msg} />
                        </>
                    )
                    }
                </div>
                <div className="col-12  flip back">
                    {/* {result && <ChallengeResultCard result={result.data} />} */}
                </div>
            </div>
        </div>
    )
}

export default Reading;