import axios from "axios";
import { useSelector } from "react-redux";
import CryptoJS from "crypto-js";
import Cookies from "js-cookie";

axios.defaults.baseURL = 'https://englishhelperapi.runasp.net/api/';

const encryptedToken = Cookies.get("token");

let decrptedToken = "";
let bytes =""; 
    if (typeof encryptedToken === 'string' && encryptedToken.length > 0) {
       bytes = CryptoJS.AES.decrypt(encryptedToken, "alaa");
      
      // Check if decryption was successful
      if (bytes && bytes.sigBytes > 0) {
        decrptedToken = bytes.toString(CryptoJS.enc.Utf8) || "";
      } else {
        console.warn("Token decryption returned empty result");
        // Clear invalid cookie
        Cookies.remove("token");
      }
    }
axios.interceptors.request.use(
    config => {
        if (decrptedToken) {
            config.headers.Authorization = `Bearer ${decrptedToken}`;
        }
        config.headers["Content-Type"]= 'multipart/form-data';
        return config;
    },
    error => Promise.reject(error)
);

