import React, { useState, useRef, useEffect, useCallback } from 'react';
import { Mic, MicOff, Play, Pause, Send, Trash2, Square } from 'lucide-react';
import { Client } from "https://cdn.jsdelivr.net/npm/@gradio/client/dist/index.min.js";

const VoiceRecorder = (props) => {
    const [isRecording, setIsRecording] = useState(false);
    const [isPlaying, setIsPlaying] = useState(false);
    const [recordingTime, setRecordingTime] = useState(0);
    const [audioURL, setAudioURL] = useState(null);
    const [audioBlob, setAudioBlob] = useState(null);
    const [playbackTime, setPlaybackTime] = useState(0);
    const [duration, setDuration] = useState(0);
    const [audioLevels, setAudioLevels] = useState(new Array(200).fill(0));
    const [currentLevel, setCurrentLevel] = useState(0);

    const mediaRecorderRef = useRef(null);
    const audioRef = useRef(null);
    const audioContextRef = useRef(null);
    const analyserRef = useRef(null);
    const dataArrayRef = useRef(null);
    const sourceRef = useRef(null);
    const streamRef = useRef(null);
    const intervalRef = useRef(null);
    const animationRef = useRef(null);
    const chunksRef = useRef([]);
    const containerRef = useRef(null);
    const [barCount, setBarCount] = useState(50); // default bar count


    useEffect(() => {
        const observer = new ResizeObserver((entries) => {
            for (let entry of entries) {
                const width = entry.contentRect.width;
                const estimatedBarCount = Math.floor(width / 5); // e.g. 3px bar + 2px gap
                setBarCount(Math.max(10, estimatedBarCount)); // prevent too few bars
            }
        });

        if (containerRef.current) {
            observer.observe(containerRef.current);
        }

        return () => {
            if (containerRef.current) {
                observer.unobserve(containerRef.current);
            }
        };
    }, []);
    useEffect(() => {
        setAudioLevels(new Array(barCount).fill(0));
    }, [barCount]);

    // Initialize audio visualization
    const initializeAudioContext = useCallback(async (stream) => {
        try {
            audioContextRef.current = new (window.AudioContext || window.webkitAudioContext)();
            analyserRef.current = audioContextRef.current.createAnalyser();
            sourceRef.current = audioContextRef.current.createMediaStreamSource(stream);

            analyserRef.current.fftSize = 256;
            const bufferLength = analyserRef.current.frequencyBinCount;
            dataArrayRef.current = new Uint8Array(bufferLength);

            sourceRef.current.connect(analyserRef.current);

            // Start visualization
            visualizeAudio();
        } catch (error) {
            console.error('Error initializing audio context:', error);
        }
    }, []);

    // Audio visualization function
    const visualizeAudio = useCallback(() => {
        if (!analyserRef.current || !dataArrayRef.current) return;

        const animate = () => {
            analyserRef.current.getByteFrequencyData(dataArrayRef.current);

            // Calculate average volume
            const average = dataArrayRef.current.reduce((a, b) => a + b) / dataArrayRef.current.length;
            const normalizedLevel = Math.min(average / 128, 1);

            setCurrentLevel(normalizedLevel);

            // Update audio levels array for visualization
            setAudioLevels(prevLevels => {
                const newLevels = [...prevLevels.slice(1), normalizedLevel];
                return newLevels;
            });

            if (isRecording) {
                animationRef.current = requestAnimationFrame(animate);
            }
        };

        animate();
    }, [isRecording]);

    // Start recording
    const startRecording = async () => {
        try {
            const stream = await navigator.mediaDevices.getUserMedia({
                audio: {
                    echoCancellation: true,
                    noiseSuppression: true,
                    autoGainControl: true
                }
            });

            streamRef.current = stream;

            // Initialize audio visualization
            await initializeAudioContext(stream);

            // Setup MediaRecorder
            mediaRecorderRef.current = new MediaRecorder(stream, {
                mimeType: 'audio/webm;codecs=opus'
            });

            chunksRef.current = [];

            mediaRecorderRef.current.ondataavailable = (event) => {
                if (event.data.size > 0) {
                    chunksRef.current.push(event.data);
                }
            };

            mediaRecorderRef.current.onstop = () => {
                const blob = new Blob(chunksRef.current, { type: 'audio/webm;codecs=opus' });
                const url = URL.createObjectURL(blob);
                setAudioURL(url);
                setAudioBlob(blob);

                // Stop all tracks
                stream.getTracks().forEach(track => track.stop());

                // Close audio context
                if (audioContextRef.current && audioContextRef.current.state !== 'closed') {
                    audioContextRef.current.close()
                }
            };

            mediaRecorderRef.current.start(100); // Collect data every 100ms
            setIsRecording(true);
            setRecordingTime(0);

            // Start timer
            intervalRef.current = setInterval(() => {
                setRecordingTime(prev => prev + 1);
            }, 1000);

        } catch (error) {
            console.error('Error starting recording:', error);
            alert('Error accessing microphone. Please check permissions.');
        }
    };

    // Stop recording
    const stopRecording = () => {
        if (mediaRecorderRef.current && isRecording) {
            mediaRecorderRef.current.stop();
            setIsRecording(false);

            // Stop timer
            if (intervalRef.current) {
                clearInterval(intervalRef.current);
            }

            // Stop animation
            if (animationRef.current) {
                cancelAnimationFrame(animationRef.current);
            }

            // Stop stream
            if (streamRef.current) {
                streamRef.current.getTracks().forEach(track => track.stop());
            }
        }
    };

    // Play/pause audio
    const togglePlayback = () => {
        if (!audioRef.current) return;

        if (isPlaying) {
            audioRef.current.pause();
        } else {
            audioRef.current.play();
        }
        setIsPlaying(!isPlaying);
    };

    // Handle audio events
    useEffect(() => {
        const audio = audioRef.current;
        if (!audio) return;

        const handleLoadedMetadata = () => {
            setDuration(audio.duration);
        };

        const handleTimeUpdate = () => {
            setPlaybackTime(audio.currentTime);
        };

        const handleEnded = () => {
            setIsPlaying(false);
            setPlaybackTime(0);
        };

        const handlePlay = () => setIsPlaying(true);
        const handlePause = () => setIsPlaying(false);

        audio.addEventListener('loadedmetadata', handleLoadedMetadata);
        audio.addEventListener('timeupdate', handleTimeUpdate);
        audio.addEventListener('ended', handleEnded);
        audio.addEventListener('play', handlePlay);
        audio.addEventListener('pause', handlePause);

        return () => {
            audio.removeEventListener('loadedmetadata', handleLoadedMetadata);
            audio.removeEventListener('timeupdate', handleTimeUpdate);
            audio.removeEventListener('ended', handleEnded);
            audio.removeEventListener('play', handlePlay);
            audio.removeEventListener('pause', handlePause);
        };
    }, [audioURL]);

    // Delete recording
    const deleteRecording = () => {
        if (audioURL) {
            URL.revokeObjectURL(audioURL);
        }
        setAudioURL(null);
        setAudioBlob(null);
        setPlaybackTime(0);
        setDuration(0);
        setIsPlaying(false);
        setRecordingTime(0);
        setAudioLevels(new Array(50).fill(0));
    };

    // Send recording
    const sendRecording = async() => {
        if (audioBlob) {
            // Here you would typically upload the audioBlob to your server
            console.log('Sending audio blob:', audioBlob);

            const client = await Client.connect("YoussefA7med/English_Helper_Reading");
            const result = await client.predict("/evaluate_reading_only", {
                passage_text:props.message,
                audio_file: audioBlob,
            });
            console.log(result.data);
            
            deleteRecording();
        }
    };

    // Format time
    const formatTime = (seconds) => {
        const mins = Math.floor(seconds / 60);
        const secs = Math.floor(seconds % 60);
        return `${mins}:${secs.toString().padStart(2, '0')}`;
    };

    // Cleanup on unmount
    useEffect(() => {
        return () => {
            if (intervalRef.current) {
                clearInterval(intervalRef.current);
            }
            if (animationRef.current) {
                cancelAnimationFrame(animationRef.current);
            }
            if (audioURL) {
                URL.revokeObjectURL(audioURL);
            }
            if (audioContextRef.current && audioContextRef.current.state !== 'closed') {
                audioContextRef.current.close()
            }
            if (streamRef.current) {
                streamRef.current.getTracks().forEach(track => track.stop());
            }
        };
    }, [audioURL]);

    const containerStyle = {
        maxWidth: '448px',
        margin: '0 auto',
        backgroundColor: 'white',
        borderRadius: '12px',
        boxShadow: '0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05)',
        padding: '24px',
        fontFamily: 'system-ui, -apple-system, sans-serif'
    };

    const headerStyle = {
        textAlign: 'center',
        marginBottom: '16px'
    };

    const titleStyle = {
        fontSize: '24px',
        fontWeight: 'bold',
        color: '#1f2937',
        marginBottom: '8px',
        margin: 0
    };

    const subtitleStyle = {
        color: '#6b7280',
        margin: 0
    };

    const visualizerContainerStyle = {
        borderRadius: '12px',
        padding: '16px',
        marginBottom: '16px'
    };

    const barsContainerStyle = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        height: '96px',
        width: '100%',
        gap: '2px'
    };

    const levelIndicatorStyle = {
        marginTop: '8px'
    };

    const progressBarContainerStyle = {
        width: '100%',
        backgroundColor: '#e5e7eb',
        borderRadius: '9999px',
        height: '8px',
        overflow: 'hidden'
    };

    const recordingTextStyle = {
        textAlign: 'center',
        fontSize: '14px',
        color: '#6b7280',
        marginTop: '4px'
    };

    const recordButtonStyle = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '64px',
        height: '64px',
        borderRadius: '50%',
        border: 'none',
        color: 'white',
        fontWeight: '500',
        cursor: 'pointer',
        transition: 'all 0.2s ease',
        transform: isRecording ? 'scale(1.1)' : 'scale(1)'
    };

    const playbackControlsStyle = {
        marginBottom: '16px'
    };

    const timeDisplayStyle = {
        display: 'flex',
        justifyContent: 'space-between',
        fontSize: '14px',
        color: '#6b7280',
        marginBottom: '8px'
    };

    // const playbackProgressStyle = {
    //     width: '100%',
    //     backgroundColor: '#e5e7eb',
    //     borderRadius: '9999px',
    //     height: '8px',
    //     overflow: 'hidden'
    // };

    const controlButtonsStyle = {
        display: 'flex',
        justifyContent: 'center',
        gap: '16px',
        marginBottom: '16px'
    };

    const controlButtonStyle = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '48px',
        height: '48px',
        borderRadius: '50%',
        border: 'none',
        color: 'white',
        cursor: 'pointer',
        transition: 'all 0.2s ease'
    };

    const statusStyle = {
        textAlign: 'center',
        fontSize: '14px',
        color: '#6b7280'
    };

    return (
        <div className='container d-flex justify-content-center align-items-center' >

            {/* Audio Visualization */}
            <div className='row flex-grow-1' style={visualizerContainerStyle}>
                <div className='col-12 '>
                    <div ref={containerRef} style={barsContainerStyle}>
                        {audioLevels.map((level, index) => {
                            const barStyle = {
                                width: '3px',
                                background: 'linear-gradient(to top, #4ade80, #16a34a)',
                                borderRadius: '2px',
                                transition: 'all 0.15s ease',
                                height: `${Math.max(4, level * 80)}px`,
                                opacity: isRecording || audioURL ? 1 : 0.3,
                                animation: isRecording ? 'pulse 1s infinite' : 'none'
                            };
                            return <div key={index} style={barStyle} />;
                        })}
                    </div>

                </div>
            </div>


            {/* Recording Controls */}
            {!audioURL && (
                <div style={{ display: 'flex', justifyContent: 'center', marginBottom: '16px' }}>
                    <button
                        onClick={isRecording ? stopRecording : startRecording}
                        style={{
                            ...recordButtonStyle,
                            backgroundColor: isRecording ? '#ef4444' : '#49BBBD',
                        }}
                        onMouseEnter={(e) => {
                            e.target.style.backgroundColor = isRecording ? '#dc2626' : '#49BBBD';
                            if (!isRecording) e.target.style.transform = 'scale(1.05)';
                        }}
                        onMouseLeave={(e) => {
                            e.target.style.backgroundColor = isRecording ? '#ef4444' : '#49BBBD';
                            e.target.style.transform = isRecording ? 'scale(1.1)' : 'scale(1)';
                        }}
                    >
                        {isRecording ? (
                            <Square size={24} />
                        ) : (
                            <Mic size={24} />
                        )}
                    </button>
                </div>
            )}

            {/* Playback Controls */}
            {audioURL && (
                <div style={playbackControlsStyle}>
                    <audio ref={audioRef} src={audioURL} preload="metadata" />

                    {/* Progress Bar */}
                    <div>
                        {/* <div style={timeDisplayStyle}>
                            <span>{formatTime(playbackTime)}</span>
                            <span>{formatTime(duration)}</span>
                        </div> */}
                        <div >
                            <div
                                style={{
                                    backgroundColor: '#49BBBD',
                                    height: '100%',
                                    borderRadius: '9999px',
                                    transition: 'all 0.3s ease',
                                    width: `${duration ? (playbackTime / duration) * 100 : 0}%`
                                }}
                            />
                        </div>
                    </div>

                    {/* Control Buttons */}
                    <div style={controlButtonsStyle}>
                        <button
                            onClick={togglePlayback}
                            style={{
                                ...controlButtonStyle,
                                backgroundColor: '#49BBBD'
                            }}
                            onMouseEnter={(e) => e.target.style.backgroundColor = '#49BBBD'}
                            onMouseLeave={(e) => e.target.style.backgroundColor = '#49BBBD'}
                        >
                            {isPlaying ? (
                                <Pause size={20} />
                            ) : (
                                <Play size={20} style={{ marginLeft: '2px' }} />
                            )}
                        </button>

                        <button
                            onClick={deleteRecording}
                            style={{
                                ...controlButtonStyle,
                                backgroundColor: '#ef4444'
                            }}
                            onMouseEnter={(e) => e.target.style.backgroundColor = '#dc2626'}
                            onMouseLeave={(e) => e.target.style.backgroundColor = '#ef4444'}
                        >
                            <Trash2 size={20} />
                        </button>

                        <button
                            onClick={sendRecording}
                            style={{
                                ...controlButtonStyle,
                                backgroundColor: '#49BBBD'
                            }}
                            onMouseEnter={(e) => e.target.style.backgroundColor = '#49BBBD'}
                            onMouseLeave={(e) => e.target.style.backgroundColor = '#49BBBD'}
                        >
                            <Send size={20} />
                        </button>
                    </div>
                </div>
            )}

            <style jsx>{`
        @keyframes pulse {
          0%, 100% { opacity: 1; }
          50% { opacity: 0.7; }
        }
      `}</style>
        </div>
    );
};

// Demo App Component
const App = (props) => {
    const appStyle = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '16px'
    };

    return (
        <div style={appStyle}>
            <VoiceRecorder message={props.message} />
        </div>
    );
};

export default App;