import CryptoJS from "crypto-js";
import Cookies from "js-cookie";

const encryptedToken = Cookies.get("token");

let decrptedToken = "";
if (encryptedToken) {
  try {
    if (typeof encryptedToken === 'string' && encryptedToken.length > 0) {
      const bytes = CryptoJS.AES.decrypt(encryptedToken, "alaa");
      
      // Check if decryption was successful
      if (bytes && bytes.sigBytes > 0) {
        decrptedToken = bytes.toString(CryptoJS.enc.Utf8) || "";
      } else {
        console.warn("Token decryption returned empty result");
        // Clear invalid cookie
        Cookies.remove("token");
      }
    }
  } catch (err) {
    console.error("Token decryption failed:", err);
  }
}

const INITIAL_VALUE = {
  decrptedToken,
};

export default function AuthReducer(state = INITIAL_VALUE, action) {

    switch (action.type) {

        case 'Token':
            return {
                ...state,
                decrptedToken: action.payload,
            }
        default:
            return state;
    }
}